<script setup lang="ts">
  import { ref, watch, onMounted } from 'vue'
  import { marked } from 'marked'

  import 'github-markdown-css/github-markdown.css'


  const editorValue    = ref<string>('')
  const perviewElement = ref<HTMLDivElement>()

  onMounted(() => {
    const ele = document.querySelector<HTMLDivElement>('.preview')
    if (ele) {
      perviewElement.value = ele
    }
  })

  watch(editorValue, () => {
    if (perviewElement.value) {
      perviewElement.value.innerHTML = marked.parse(editorValue.value)
    }
  })


</script>

<template>
  <div class="editor-perview">
    <div class="editor">
      <textarea placeholder="editor" v-model="editorValue" autofocus></textarea>
    </div>
    <div class="grp"></div>
    <div class="preview markdown-body"><div>preview</div></div>
  </div>
</template>

<style lang="scss" scoped>
  @import './variables.scss';


  .editor-perview {
    height: 100vh;

    display: flex;
    justify-content: space-between;

    > .grp {
      width: $grp-size;
      margin: 0 0.5rem;
      cursor: e-resize;
      box-shadow:
        -0.05rem 0px 0.05rem 0px rgba(0,0,0,.3),
        0.05rem 0px 0.05rem 0px rgba(0,0,0,.3),
      ;

      -webkit-user-select: none;
      user-select: none;
